.blur__page {
    filter: blur(6px);
    opacity: .7;
    transition: .3s linear;
}

.blur__header {
    background: #ffffffd1;
}

.div-block-6 {
    font-family: Quicksand ,sans-serif;
}

#blur {
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: none;
  }
  .acrylic {
    background: none;
  }
  
  .acrylic::before {
    content: "";
    top: -30px;
    right: -30px;
    width: calc(100% + 60px);
    height: calc(100% + 60px);
    filter: blur(30px) saturate(125%);
    z-index: -1;
  }
  
  .acrylic::after {
    content: "";
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: #ffffffd1
      url(data:image/png;base64,);
    opacity: 0;
    z-index: -1;
  }
  
  @supports (backdrop-filter: blur(30px)) or (-webkit-backdrop-filter: blur(30px)) {
    .acrylic {
      -webkit-backdrop-filter: blur(30px) saturate(125%);
      backdrop-filter: blur(30px) saturate(125%);
    }
  
    .acrylic::before {
      display: none;
    }
  }
  
  @media all and (display-mode: standalone) {
    div.screen {
      display: none;
    }
  }
  

